import SideItem from './SideItem'

function Side(props) {
    var list = [
        {
            title: 'Danh mc',
            cate: 'cate',
            listItem: [
                {
                    name :'in thoi',
                    value: 'PHONE'
                }, 
                {
                    name :'My tnh',
                    value: 'LAPTOP'
                },
                {
                    name :'Loa & Tai nghe',
                    value: 'SOUND'
                },
                {
                    name :'Ph kin',
                    value: 'ACCESSORIES'
                },
                {
                    name :'ng h',
                    value: 'WATCH'
                }, 
                {
                    name :'Tt c',
                    value: 'all'
                },
            ]
        },
        {
            title: 'Gi',
            cate: 'price',
            listItem: [
                {
                    name :'Di 2 triu',
                    value: '0-2000000'
                },
                {
                    name :'T 2 - 4 triu',
                    value: '2000000-4000000'
                },
                {
                    name :'T 4 - 7 triu',
                    value: '4000000-7000000'
                },
                {
                    name :'T 7 - 13 triu',
                    value: '7000000-13000000'
                },
                {
                    name :'Trn 13 triu',
                    value: '13000000-100000000'
                },
                {
                    name :"Tt c",
                    value: 'all'
                }
            ]
        },
    ];

    return ( 
        <div class="side-option">
            {
                list.map( (item,index) => {
                    return <SideItem getListCate={props.getListCate} key={index} title={item.title} cate={item.cate} listItem={item.listItem}></SideItem>
                    }
                )
            }
        </div> 
    );
}

export default Side;