import React from 'react';
function BookPopup(props) {
    const [isvalid, setIsvalid] = React.useState(true);
    const [inputValue, setInputValue] = React.useState('');
    var pattern = /^[0-9]*$/i;

    function handleOnchange (event) {
        var result = (event.target.value.replace(/\./g,'')).match(pattern);
        if(result !== null){
            setIsvalid(true)
        }else{
            setIsvalid(false)
        }
        setInputValue(event.target.value.toString().replace(/\./g,'').replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1."))
    }
    return ( 
        <div class={"book-popup fade " + (props.displayPopup ?  '' : 'un-fade')}>
            <div class="book-popup_container">
                <p class="title">t li nhc</p>
                <div class="present-price">
                    <p class="present-price_title">Gi hin ti</p>
                    <p>{(props.price ? props.price
                        .toString()
                        .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1.") : '') + ' '}
                       VN</p>
                </div>
                <div class="expected-price">
                    <p class="expected-price_title">Gi mong mun</p>
                    <input onChange={handleOnchange} id="priceInput" value={inputValue} />
                    <span>VN</span>
                    {!isvalid ? <p class="error-text">Gi tr khng ph hp</p> : ''}
                </div>
                <div class="group-button">
                    <p class="cancel" onClick={props.handleDisplayPopup}>Hy</p>
                    <p class="accept">ng </p>
                </div>
            </div>
        </div>
     );
}

export default BookPopup;